.App {
  text-align: center;
  background-color: #081227;
  min-height: 100vh;
}

.App-logo {
  height: 20vmin;
}

.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 5vmin);
  color: white;
}

.App-info {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px);
  color: white;
}

.stream-status {
  font-size: calc(12px);
  color: white;
  margin-bottom: 5px;
}

.App-footer {
  min-height: 8vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(12px);
  color: white;
}

a {
  color: #09d3ac;
  text-decoration: none;
}
